
<template>
  <div id="app">  
    <div class="layout">
      <Layout>
          <Header>
            <div class = "header">
              <h1 class='fl'>{{$t('lang.app.header')}}</h1>
              <div class='langue'>
                <span @click="changeLang('en')" :class='isEng && "color"'>English</span> | 
                <span @click="changeLang('sp')" class="noColor">Spanish</span>
              </div>
              
            </div>
          </Header>
          <div class="menu-box">
            <div class='menu'>
              <Menu  mode="horizontal" ref="e-menu"  :open-names='menu.openNames' :active-name='menu.activeName' @on-select='onMenuSelect' :theme='theme'>
                <MenuItem :name="menu.list[0].name">
                    {{$t('lang.menu.home')}}
                </MenuItem>
                <MenuItem :name="menu.list[1].name">
                    {{$t('lang.menu.about')}}
                </MenuItem>
                <Submenu :name="menu.list[2].name">
                    <template slot="title">
                        {{$t('lang.menu.product')}}
                    </template>
                    <MenuItem :name="item.name" :key="item.name" v-for="(item, i) in productMenuList">{{$t('lang.product.productMenu['+i+']')}}</MenuItem>
                </Submenu>
                <Submenu :name="menu.list[3].name">
                    <template slot="title">
                        {{$t('lang.menu.project')}}
                    </template>
                    <MenuItem :name="item.name" v-for="(item, i) in projectMenuList" :key='item.name'>{{$t('lang.project.projectMenu['+i+']')}}</MenuItem>
                </Submenu>
                <MenuItem :name="menu.list[4].name">
                    {{$t('lang.menu.contact')}}
                </MenuItem>
              </Menu>
            </div>
          </div>
          <Content>
            <div class="content">
              <router-view/>
            </div>
          </Content>
          <Footer class="layout-footer-center">shanghuatina@outlook.com</Footer>
      </Layout>
    </div>
  </div>
</template>
<script>
import {menuE} from '@/constant/menu'
import {productMenuList} from "./components/Product/list.config"
import {projectMenuList} from "./components/Project/list.config"
export default {
  name: 'App',
  data(){
    return{
      theme: 'primary',
      menu: menuE,
      productMenuList,
      projectMenuList,
      isEng: true
    }
  },
    mounted: function () {
    // this.menu.list = this.G_CONST.menuList
  },
   watch: {
    '$route' (to, from) {
      this.setActiveName(to.path)
    }
  },
  methods: {
    changeLang(type) {
      if(type === 'en'){
        this.$i18n.locale = 'en'
        this.isEng = true
      }else{
        this.$i18n.locale = 'sp'
        this.isEng = false
      }
    },
    onMenuSelect (path) {
      // console.log(path)
      // hash:path.split('/')[2]
      this.$router.push({path})
    },
    setActiveName (path) {
      const matched = path.match(/(\/[\w\-_]+)/g)
      // console.log(path.split('/', 2))
      if (matched) {
          // this.menu.activeName = '/'
          // this.menu.openNames = ['/']
          // if (path.split('/', 2)[1] === 'home') {
          //   this.menu.activeName = '/'
          //   this.menu.openNames = ['/']
          // } else {
            this.menu.activeName = '/' + path.split('/', 2)[1]
            this.menu.openNames = ['/' + path.split('/', 2)[1]]
          // }
        }
      // this.$nextTick(() => {
      //   this.$refs['e-menu'].$children[0].updateOpened()
      // })
    }
  }
}
</script>

<style  scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
  height:100%;
  background: rgb(221, 220, 220);

}

.menu-box{
  background: #3f72af;
  width:100%;
}
.menu{
  margin:0  auto;
  background: #3f72af;
  width:800px;
  /* text-align: center */
}
.content{
  /* margin-top:64px; */
  /* margin-bottom: 64px; */
  height:100%; 
  min-height:calc( 100vh - 193px )
}
.header{
      height:64px;
      color: #fff;
      text-align: left;
      width: 1000px;
      margin: 0 auto;
      /* border-bottom: 1px solid rgb(206, 204, 204); */
    }
.footer{
  height:64px;
  border-top: 1px solid rgb(206, 204, 204);
  /* background: #2c3e50; */
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
.layout-footer-center{
    text-align: center;
}
.ivu-layout-footer {
    background: #2c3e50;;
    padding: 24px 50px;
    color: #6a7285;
    font-size: 14px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.langue{
  float: right;
  cursor: pointer;
}
.color {
  color: '#fff'
}
.noColor {
  color: '#000'
}
</style>



