<template>
  <div>
    <Row>
      <Col span='14'>
        <Carousel autoplay v-model="value2" loop>
          <CarouselItem v-for="item in obj.imgList" :key='item'>
              <div class="demo-carousel">
                <img class='img' :src="getImgSrc(item)" alt="">
              </div>
          </CarouselItem>
        </Carousel>
      </Col>
      <Col span='10'> 
        <div class='left-box'>
          <h2 class='left-title'>{{$t('lang.product.proDetail.specifications')}}:</h2>
          <div v-for="(val,key,i) in $t('lang.product.proDetail.detail-'+id+'.obj.techTitle')">
            <Tag checkable color="blue">{{key}}：</Tag>  {{val}}<br/>
          </div>
        </div>
      </Col>
    </Row>
    <Row style="margin-top:20px;line-height: 26px;text-align: left;">
      <Tabs type="card">
        <TabPane :label="$t('lang.product.proDetail.overview')" v-html="$t('lang.product.proDetail.detail-'+id+'.obj.overview')">
        </TabPane>
        <TabPane :label="$t('lang.product.proDetail.technical')">
          <img :src="getImgSrc($t('lang.product.proDetail.detail-'+id+'.obj.technical'))" alt="">
        </TabPane>
    </Tabs>
    </Row>
  </div>
</template>

<script>
import {detailList} from "../list.config"

export default {
  name: 'productDetail',
  created(){
    let id = this.$route.params.id
    this.id = id
    this.selectObj(id)
  },
  mounted(){
    // document.body.scrollTop = document.documentElement.scrollTop = 0
  },
  data () {
    return {
      value2:0,
      id:0,
      obj: {}
    }
  },
  methods:{
    getImgSrc(path){
      return require('../img/'+path)
    },
    selectObj(id){ 
      let arr = detailList.filter((item)=> item.id === id * 1)
      this.obj = arr[0].obj
    }
  }
}

</script>
<style scoped lang='less'>
.demo-carousel{

  width:100%;
  text-align: center;

  .img{
    width:100%;
    height:auto;
  }
}
.left-title{
  text-align: left;
  margin-bottom: 20px;
}
.left-box{
  font-size: 14px;
  color: #666;
  line-height: 30px;
  text-align: left;
  padding-left:20px;
}
  </style>


