<template>
  <div class="box">
      <Carousel autoplay v-model="value" loop>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner1.jpg" alt="">
            </div>
        </CarouselItem>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner2.jpg" alt="">
            </div>
        </CarouselItem>
        <CarouselItem>
            <div class="demo-carousel">
              <img src="../../assets/banner3.jpg" alt="">
            </div>
        </CarouselItem>
    </Carousel>
    <Row class="content">
      <Divider orientation="left">
        <span class='pro-title'>{{$t('lang.home.title')}}</span>
      </Divider>
      <Row >
        <Col span = "6" class='li-box' v-for="(item, i) in productImgList" :key = "item.id">
          <div @click="goDetail(item)">
            <img class='li-box-img' :src="getImgSrc(item.imgSrc)" alt="">
            <div class='li-title-box'>
              <span>{{$t('lang.home.homePro['+i+']')}}</span>
            </div>
          </div>
        </Col>
      </Row>
    </Row>
  </div>
</template>

<script>
import {productImgList} from "./list.config"
export default {
  name: 'Home',
  data () {
    return {
     value: 0,
     productImgList
    }
  },
  methods:{
    getImgSrc(path){
      return require('../Product/img/'+path)
    },
    getBannerImg(path){
      return require('../../assets/'+path)
    },
    goDetail(item){
      this.$router.push({
        name:'productDetail',
        params:{
          id:item.id
        }
      })
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.demo-carousel{
  /* width:100%; */
  width:100%;
	// height:300px;
  /* background:pink; */
  /* text-align: center; */
  /* line-height: 300px; */
}

  .content{
    width:1000px;
    margin: 15px auto;
    .box{
      text-align: center
    }
  }
.li-box{
  /* float: left; */
  /* background:blueviolet; */
  width: 230px;
  height: 200px;
  margin:10px;
}
.li-box:hover {
    transform: scale(1.1);
}
.li-box{
    box-shadow:2px 4px 8px #999;
    transition: all 0.3s linear;/*之前讲过的transition，他们俩配合起来，样式无敌啊*/
}
.li-box-img{
  display: block;
  height: 170px;
  width: 230px;
}
.li-title-box{
  background:#bbc5d6;
  height:30px;
  font-size: 14px;
  text-align: left;
  color: #fff;
  line-height: 30px;
  padding-left:10px;
}
.pro-title {
  font-size: 26px;
    font-family: -webkit-pictograph;
    font-weight: 900;
}
</style>



