<template>
  <Row >
    <Col span = "6" class='li-box' :key="item.imgSrc" v-for="item in productList">
      <div @click="goDetail(item)">
        <img class='li-box-img' :src="getImgSrc(item.imgSrc)" alt="">
        <div class='li-title-box'>
          <span>{{$t('lang.product.productList.'+item.text)}}</span>
        </div>
      </div>
    </Col>
  </Row>
</template>

<script>
import {productList1,productList2,productList3,productList4,productList5,productList6} from "../list.config"

export default {
name: 'productList',
  mounted(){
    this.updata(this.id)
  },
   watch: {
    //正确给 cData 赋值的 方法
    id: function(newVal,oldVal){
        this.id = newVal;  //newVal即是chartData
        newVal && this.updata(newVal); //newVal存在的话执行drawChar函数
    }
  },
  props: {
    id:{
      type: String
    }
  },
  data () {
    return {
      productList:[]
    }
  },
  methods:{
    getImgSrc(path){
      return require('../img/'+path)
    },
    goDetail(item){
      this.$router.push({name:'productDetail',params:{id:item.id}})
    },
    updata(id){
      let list = []
       switch (id * 1){
        case 1:
          list = productList1
          break
        case 2:
          list = productList2
          break
        case 3:
          list = productList3
          break
        case 4:
          list = productList4
          break
        case 5:
          list = productList5
          break
        case 6:
          list = productList6
          break
      }
      this.productList = list
    }
  }
}

</script>
<style scoped lang='less'>
  .li-box{
    /* float: left; */
    /* background:blueviolet; */
    width: 230px;
    height: 210px;
    margin:10px;
    .li-box-img{
      display: block;
      height: 180px;
      width: 230px;
    }
    .li-title-box{
      background:#bbc5d6;;
      height:30px;
      font-size: 14px;
      text-align: left;
      color: #fff;
      line-height: 30px;
      padding-left:10px;
    }
  }
  </style>


